import "./index.scss";
const buttons = {
    summer: document.getElementById("summer-button"),
    rain: document.getElementById("rain-button"),
    winter: document.getElementById("winter-button"),
};
const volumeControl = document.getElementById("volume");
let currentSound = null;
let currentTheme = null;
const soundPaths = {
    summer: "./assets/sounds/summer.mp3",
    rain: "./assets/sounds/rain.mp3",
    winter: "./assets/sounds/winter.mp3",
};
const backgroundImages = {
    summer: "url('./assets/images/summer-bg.jpg')",
    rain: "url('./assets/images/rainy-bg.jpg')",
    winter: "url('./assets/images/winter-bg.jpg')",
};
function createAndPlaySound(theme) {
    const sound = new Audio(soundPaths[theme]);
    sound.loop = true;
    sound.volume = volumeControl?.valueAsNumber || 0;
    playSound(sound, theme);
    document.body.style.backgroundImage = backgroundImages[theme];
}
function playSound(sound, theme) {
    stopSound();
    currentSound = sound;
    currentTheme = theme;
    sound.play();
}
function stopSound() {
    if (currentSound) {
        currentSound.pause();
        currentSound.currentTime = 0;
        currentSound = null;
        currentTheme = null;
    }
}
function handleButtonClick(theme) {
    if (currentTheme === theme) {
        stopSound();
        document.body.style.backgroundImage = "";
    }
    else {
        createAndPlaySound(theme);
    }
}
Object.entries(buttons).forEach(([theme, button]) => {
    if (button) {
        button.addEventListener("click", () => handleButtonClick(theme));
    }
});
volumeControl?.addEventListener("input", () => {
    if (currentSound) {
        currentSound.volume = volumeControl.valueAsNumber || 0;
    }
});
